.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #333;
    color: #fff;
    padding: 1rem;
  }
  
  .menu a {
    color: #fff;
    margin-right: 1rem;
    text-decoration: none;
  }
  
  .menu a:hover {
    text-decoration: underline;
  }